.about-services {
	margin-top: -20px;
	overflow: hidden;
	padding: 0 0 60px;

	@media (min-width: 768px) {
		margin: 0;
	}

	@media (min-width: 1200px) {
		padding: 0 0 80px;
	}

	@media (min-width: 1600px) {
		padding-bottom: 80px !important;
	}


	.shadow-btn-wrap {
		margin-top: 0;

		@media (min-width: 768px) {
			margin-top: 10px;
		}
	}
}

.about-services__wrap {
	display: flex;
	//gap: 20px;
	flex-wrap: wrap;

	@media (min-width: 1200px) {
		display: block;
	}
}

.about-services__item {
	box-sizing: border-box;
	border: 1px solid rgb(230, 221, 238);
	border-radius: 6.89px;
	padding: 14px 20px;
	word-break: break-word;
	width: 100%;
	position: relative;
	color: #64458D;
	overflow: hidden;
	margin-bottom: 20px;

	@media (min-width: 1200px) {
		padding: 25px 25px;
	}

	&::before {
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
		background-image: url('../img/decor/disk-decor.png');
		position: absolute;
		content: "";
		top: 16px;
		right: -58px;
		width: 173px;
		height: 100%;
		background-color: transparent;
		transform: rotate(7deg);

		@media (min-width: 768px) {
			top: -5px;
			right: -23px;
			width: 215px;
			height: 130%;
			transform: rotate(25deg);
		}

		@media (min-width: 1800px) {
			right: -59px;
			width: 266px;
		}
	}
}

.about-services__title {
	margin: 0;
	position: relative;
	z-index: 2;

	@media (min-width: 1400px) {
		font-size: 44px;
	}
}

.about-services__slider-wrap {
	width: calc(100% + 1.5rem);
	position: relative;
	left: -0.75rem;

	@media (min-width: 576px) {
		width: calc(100% + 18rem);
		position: relative;
		left: -9rem;
	}
}

.aboutServicesSwiper {
	width: 100%;
	height: 100%;
	padding-bottom: 84px;
	position: relative;

	@media (min-width: 768px) {
		padding-top: 20px;
		padding-bottom: 100px;
	}


	&::before {
	  position: absolute;
	  content: '';
	  bottom: -3px;
	  left: 0;
	  width: 100%;
	  height: 88px;
		z-index: 2;
		background: rgb(255,255,255);
		background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0.4990371148459384) 8%, rgba(255,255,255,1) 20%);

		@media (min-width: 768px) {
			bottom: -18px;
			height: 130px;
		}
	}

	.swiper-wrapper {
		margin-left: 0.75rem;

		@media (min-width: 768px) {
			//margin-left: 1.5rem;
			margin-left: 0;
		}
	}

	.swiper-slide {
		overflow: hidden;
		border-radius: 10px;
		width: 300px !important;

		@media (min-width: 375px) {
			//height: 300px;
		}

		@media (min-width: 576px) {
			//height: 280px;
			width: calc(100% - 400px) !important;
		}

		@media (min-width: 768px) {
			height: unset;
			width: calc(100% - 480px) !important;
		}

		@media (min-width: 992px) {
			width: calc(100% - 880px) !important;
		}
	}

	.swiper-scrollbar__wrap {
		position: relative;
		width: calc(100% - 130px);
		left: 50%;
		transform: translateX(-50%);
		z-index: 4;

		@media (min-width: 576px) {
			width: calc(66% - 130px);
		}

		@media (min-width: 768px) {
			width: calc(100% - 400px);
			transform: translateX(-50%);
		}

		@media (min-width: 1200px) {
			position: relative;
			bottom: -26px;
			width: 400px;
			transform: translateX(-30%);
		}

		@media (min-width: 1400px) {
			transform: translateX(-24%);
		}
	}

	.swiper-scrollbar {
		bottom: -41px;

		@media (min-width: 768px) {
			bottom: -57px;
		}

		&::before, &::after {
			width: 70px;
			display: none;

			@media (min-width: 768px) {
				width: 75px;

			}
		}
	}

	.swiper-button-next,
	.swiper-button-prev {
		top: auto;
		bottom: 24px
	}

	.swiper-button-next {
		right: 0.75rem;

		@media (min-width: 576px) {
			right: calc(50% - 258px);
		}

		@media (min-width: 768px) {
			right: calc(50% - 348px);
		}

		@media (min-width: 992px) {
			right: calc(50% - 468px);
		}
	}

	.swiper-button-prev {
		left: 0.75rem;

		@media (min-width: 576px) {
			left: calc(50% - 258px);
		}


		@media (min-width: 768px) {
			left: calc(50% - 348px);
		}

		@media (min-width: 992px) {
			left: calc(50% - 468px);
		}
	}

}

.about-services-card {
	border: 1px solid rgb(230, 221, 238);
	border-radius: 16px;
	background: rgb(255, 255, 255);
	padding: 16px 17px;
	//height: 100%;
	width: 100%;

	@media (min-width: 1200px) {
		&:not(:last-child) {
			margin-bottom: 24px;
		}

		padding: 20px 20px;
	}

	ul {
		padding-left: 22px;
		margin-bottom: 0;

		li {
			&:not(:last-child) {
				margin-bottom: 0;
			}

			@media (min-width: 1600px) {
				font-size: 14px;
			}
		}
	}
}

.about-services-card__title {
	font-size: 18px;
	font-weight: bold;

	@media (min-width: 1200px) {
		font-size: 20px;
	}

	@media (min-width: 1600px) {
		font-size: 16px;
		margin-bottom: 10px;
	}
}

.about-services__btn-wrap {
	@media (min-width: 1200px) {
		margin-top: 50px;
		display: flex;
		justify-content: center;
	}
}

.about-services__row {
	@media (min-width: 1600px) {
		margin-top: 60px;
	}
}