.custom-checkbox {
  display: inline-block;
  position: relative;
  cursor: pointer;
  width: 23px;
  height: 23px;
  //padding-left: 30px; /* Расстояние между кастомным чекбоксом и текстом */

  input {
    display: none;
  }

  &::before {
    content: "";
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
    width: 23px;
    height: 23px;
    border: 2px solid #6489FF;
    background-color: transparent;
    border-radius: 4px;
  }

  &::after {
    content: "";
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
    color: #fff;
    font-size: 16px;
    visibility: hidden;
    background-image: url('../images/svg/check.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 15px;
    width: 100%;
    height: 100%;
  }
}

.accent {
  color: var(--accent-text) !important;
}

.custom-checkbox {
  &.active {
    &::before {

    }

    &::after {
      visibility: visible;
    }
  }
}

