}

.swiper {
  width: 100%;
  height: 100%;

  .swiper-scrollbar {
    height: 1px;
    position: relative;
    z-index: -1;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.05);

    &::before, &::after {
      position: absolute;
      content: '';
      top: -2px;
      width: 50px;
      height: 6px;
      //background-color: var(--bs-body-bg);
      background-color: rgba(255,0,0,0.5);
      //background-color: red;
      z-index: 2;
      //display: none;
    }

    &::before {
      left: -75px;

      @media (min-width: 768px) {
        left: 0;
      }
    }

    &::after {
      right: -70px;

      @media (min-width: 768px) {
        right: 0;
      }
    }

    .swiper-scrollbar-drag {
      height: 3px;
      position: relative;
      top: -1px;
      background-color: rgb(108, 109, 176);
    }
  }

  .swiper-button-next, .swiper-button-prev {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    background-color: var(--bs-body-bg);
    top: var(--swiper-navigation-top-offset, 50%);
    transition: all .1s ease-out;
    border: 0.68px solid rgba(2, 112, 204, 0.12);

    @media (min-width: 1200px) {
      width: 60px;
      height: 60px;
    }

    &::after {
      color: transparent;
      font-size: 0;
      //transition: all .5s ease-in;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      width: 24px;
      height: 12px;

      @media (min-width: 1400px) {
        font-size: 21px;
      }
    }

    &:hover {
      background: linear-gradient(32deg, #f7c6f3 5%, #f9bbf3 20%, #6ce0ff 70%, #c7f1fd 100%);
      border: none;

      &::after {
        filter: brightness(100);
        transition: all .1s ease-in;
      }
    }

    &:active {
      //background-color: var(--input-border);
      transition: all .1s ease-in;
      background: linear-gradient(32deg, #fd7df0 5%, #ef6ce1 20%, #11b8e7 70%, #84e4ff 100%);
      background-size: 120% 130%;
      border: none;

      &::after {
        filter: brightness(100);
      }
    }
  }

  .swiper-button-next {
    right: 0;

    &::after {
      background-image: url('../img/icons/arrow-right.svg');
    }

    @media (min-width: 1400px) {
      right: var(--swiper-navigation-sides-offset, 6px);
    }
  }

  .swiper-button-prev {
    left: 0;

    &::after {
      background-image: url('../img/icons/arrow-left.svg');
    }

    @media (min-width: 1400px) {
      left: var(--swiper-navigation-sides-offset, 6px);
    }
  }

  .swiper-button-disabled {
    opacity: 1;
    background-color: #e9e9e9;
    border-color: #e9e9e9;
    cursor: not-allowed;

    &::after {
      filter: brightness(100) brightness(0.7);
    }

    &:hover {
      cursor: not-allowed;
    }
  }
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

