.visually-hidden, /* - если убрать, то в IE11 лэйблы скачут */
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}


.navbar-toggler {
  border: none !important;
  padding: 10px;
  position: relative;
  right: -10px;
  z-index: 10;

  &:focus {
    border: none !important;
    box-shadow: none;
  }

  .toggler-icon {
    width: 28px;
    height: 3px;
    border-radius: 0;
    background-color: var(--accent-color);
    display: block;
    transition: all 0.2s;
    position: relative;
  }

  .top-bar {
    transform: rotate(0);
  }

  .middle-bar {
    opacity: 1;
    filter: alpha(opacity=100);
    margin: 5px auto;
  }

  .bottom-bar {
    transform: rotate(0);
  }

  &[aria-expanded="true"] {
    .toggler-icon {
      //background-color: #fff;
      //width: 37px;
    }

    .top-bar {
      transform: translateX(25%) rotate(45deg);
      transform-origin: 10% 10%;
      top: .5px;
      left: -3px;
    }

    .middle-bar {
      opacity: 0;
      filter: alpha(opacity=0);
    }

    .bottom-bar {
      transform: translateX(25%) rotate(-45deg);
      transform-origin: 10% 90%;
      bottom: .5px;
      left: -3px;
    }
  }
}

textarea {
  display: block;
}

input, textarea, select {
  background-color: var(--input-background);
  color: var(--input-text);
  padding: 13px 18px;
  font-size: 14px;
  border-radius: var(--input-radius);
  border: 0.56px solid var(--input-border);

  @media (min-width: 768px) {
