.custom-select-container {
  max-width: 100% !important;
  overflow: hidden;
  width: 100%;

  .select2-container {
    max-width: 100% !important;
    width: 100% !important;
  }

  .select2-container--open .select2-dropdown--below {
    border-bottom-left-radius: var(--input-radius);
    border-bottom-right-radius: var(--input-radius);
    border-top: none;
  }

  .select2-container--open .select2-dropdown--above {
    border-top-left-radius: var(--input-radius);
    border-top-right-radius: var(--input-radius);
    border-bottom: none;

  }

  .select2-selection {
    background-color: var(--input-background);
    color: var(--input-text);
    padding: 8px 10px;
    font-size: 14px;
    border-radius: var(--input-radius);
    border: 0.56px solid var(--input-border);
    height: 46px;

    @media (min-width: 768px) {
      font-size: 16px;
      height: 52.95px;
      padding: 12px 10px;
    }

    @media (min-width: 992px) {
      font-size: 18px;
      padding: 17px 26px;
      height: 60px;
    }

    @media (min-width: 1200px) {
      padding: 17px 26px;
    }

    @media (min-width: 1400px) {
      padding: 16px 26px;
    }
  }

  .select2-selection__rendered {
    color: #fff !important;
  }

  .select2-search.select2-search--dropdown {
    padding: 10px;

    @media (min-width: 1400px) {
      padding: 15px 18px;
    }
  }

  .select2-search--dropdown .select2-search__field {
    border-radius: 4px;
    height: 40px;
    padding: 8px 18px;
    //margin: 5px 0;
    border-color: #fff;
    max-width: 100%;


    &:focus {
      border-color: #fff;
      outline: none !important;
    }
  }

  .select2-dropdown {
    background-color: rgba(69, 27, 139, 0.95);
    border: 0.56px solid var(--input-border);
  }

  ul {
    li {
      padding: 10px !important;

      @media (min-width: 1400px) {
        font-size: 16px;
        padding: 10px 18px !important;
      }
    }
  }

  .select2-container--default .select2-results__option--selected {
    color: #fff;
    background-color: #5494f7;
  }

  .select2-container--default .select2-results__option--highlighted.select2-results__option--selectable {
    background-color: #fff;
    color: var(--bs-font-color);
  }

  .select2-results__options {
    @media (min-width: 1200px) {
      max-height: 330px !important;
    }
  }

  .select2-selection__arrow {
    b {
      display: none;
    }

    background-image: url('../img/icons/select-arrow.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    top: calc(50%) !important;
    transform: translateY(-50%);
    right: 20px !important;
    width: 14px !important;
    height: 20px !important;
    //transition: all .2s ease;

    @media (min-width: 1400px) {
      width: 20px !important;
      height: 30px !important;
      right: 30px !important;
    }
  }

  .select2-container--open {
    .select2-selection__arrow {
      transform: rotate(180deg) translateY(50%);
    }
  }
}

.btn {
  display: inline-block;
  //border: 1px solid rgba(255, 255, 255, 0.28);
  border-radius: 100px;
  padding: 11px 33px;
  //background: linear-gradient(232deg, #0071cd 6.2%, #9b0b9d 100%), #e1eafd;
  font-weight: 700;
  font-size: 14px;
  text-align: center;
  background-color: transparent;
  box-shadow: 0 4px 15px rgba(114, 224, 255, 0.5), 0 4px 15px rgba(255, 112, 239, 0.5);
  color: #fff;
  text-decoration: none;
  transition: all .3s ease;
  position: relative;
  overflow: hidden;
  border: none;

  &::before {
    border-radius: 100px;
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: red;
    background: linear-gradient(16deg, #fd7df0 5%, #ef6ce1 20%, #11b8e7 70%, #84e4ff 100%);
    background-size: 200% 200%;
    transition: all .3s ease;
    //transform: rotate(0deg) scale3d(4,4,1);
  }

  &::after {
    position: absolute;
    content: '';
    top: -100%;
    left: -100%;
    width: 30px;
    transform: rotate(45deg);
    height: 300%;
    background-color: #fff;
    opacity: 0.5;
    transition: all .3s ease;
  }

  &:hover {
    &::before {
      background-size: 100% 100%;
      //transform: scaleX(120%);
      //background: linear-gradient(16deg, #fd7df0 5%, #ef6ce1 20%, #11b8e7 70%, #84e4ff 100%);
      //background: linear-gradient(16deg, #84e4ff 5%, #11b8e7 20%, #ef6ce1 70%, #fd7df0 100%);
      //transition: all 0.5s ease;
      //animation: gradient-animation 5s ease infinite;
    }

    &::after {
      animation: slide-right .8s ease-in-out forwards, blink .5s ease-in-out forwards;
    }
  }

  * {
    position: relative;
    z-index: 2;
  }

  &:hover {
    box-shadow: 0 6px 20px rgba(114, 224, 255, 0.5), 0 6px 20px rgba(255, 112, 239, 0.5);
    color: #fff;
  }
  
  &:active, &:focus {
    color: #fff;

    span {
      color: #fff;
    }
  }

  @media (min-width: 768px) {
    font-size: 18px;
    padding: 14px 33px;
  }

  @media (min-width: 992px) {
    font-size: 22px;
  }

  &--icon {
    position: relative;
    padding: 0;

    svg {
      width: 34px;
      height: 29px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
  }

  &.no-shadow {
    box-shadow: none;

    &:hover {
      box-shadow: 0 6px 20px rgba(114, 224, 255, 0.5), 0 6px 20px rgba(255, 112, 239, 0.5);
    }
  }
}

@keyframes slide-right {
  0% {
    left: -100%;
  }
  100% {
    left: 200%;
  }
}

@keyframes blink {
  0%, 100% {
    opacity: 0.5;
  }
  50% {
    opacity: 0.2;
  }
}

.full-height {
  @media (min-width: 1600px) {
    height: calc(100vh - 119px);
  }
}



