.modal {
	input, textarea, select {
		background-color: #f0f0f0;
		color: #000;
		font-weight: 500;
		border-color: transparent;
		padding: 20px 20px;
		font-size: 18px;

		&::placeholder {
			color: var(--bs-font-color);
		}

		&:focus {
			outline-color: #c5c5c5;
		}

	}

	form {
		label {
			width: 100%;
			margin-bottom: 20px;

			@media (min-width: 576px) {
				margin-bottom: 30px;
			}
		}

		input {
			width: 100%;
		}

		.btn {
			margin-left: auto;
			margin-right: auto;
			padding: 14px 45px;
			font-size: 18px;
		}
	}
}

.modal-content {
	//background-image: url('../img/decor/main-hero-back.png');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	--bs-modal-border-radius: 20px;
	border-radius: var(--bs-modal-border-radius);
	//color: #fff;
	position: relative;
}

.modal-header {
	display: flex;
	justify-content: flex-start;
	padding-top: 25px;
	padding-left: 30px;
	padding-right: 30px;

	@media (min-width: 576px) {
		padding-top: 30px;
		padding-left: 40px;
		padding-right: 40px;
	}

	.close {
		position: absolute;
		top: 15px;
		right: 15px;
		border: none;
		border-radius: 50%;
		min-width: 41px;
		min-height: 41px;
		max-width: 41px;
		max-height: 41px;
		transition: all .3s ease;

		@media (min-width: 576px) {
			top: 25px;
			right: 25px;
		}

		span {
			font-size: 41px;
			line-height: 38px;

			@media (min-width: 768px) {
				font-size: 41px;
				line-height: 38px;
			}
		}

		&:hover {
			transform: rotate(90deg);
		}
	}
}

.modal-title {
	font-size: 18px;

	@media (min-width: 576px) {
		font-size: 22px;
	}
}

.modal-header,
.modal-footer {
	border: none;
}

.modal-body {
	padding-bottom: 30px;
	padding-left: 25px;
	padding-right: 25px;

	@media (min-width: 576px) {
		padding-bottom: 35px;
		padding-left: 40px;
		padding-right: 40px;
	}
}