.our-leads {
	&::before {
		@media (max-width: 575px) {
			top: -8px;
		}
	}

	.custom-select-container {
		margin-bottom: 15px;

		.select2-selection {
			border-color: #fff;
			background-color: transparent;

			@media (min-width: 1400px) {
				height: 73px;
				padding: 20px 18px;
			}
		}
	}

	.our-offers__title {
		@media (min-width: 768px) {
			margin-top: 120px;
		}

		@media (min-width: 992px) {
			margin-top: 104px;
		}

		@media (min-width: 1200px) {
			margin-top: 160px;
		}

		@media (min-width: 1600px) {
			margin-top: 0;
		}
	}

	.our-offers__col-1 {
		@media (min-width: 1700px) {
			width: 40%;
		}
	}

	.our-offers__col-2 {
		@media (min-width: 1700px) {
			width: calc(73% - 10vw);
		}
	}

	.custom-select-container .select2-container {
		@media (min-width: 1900px) {
			max-width: 80% !important;
			width: 80% !important;
		}
	}

	.our-offers__slider-wrap {
		@media (min-width: 1900px) {
			left: 2rem;
			width: calc(100% + 4.25rem);
		}
	}

	.leadsSwiper .swiper-slide {
		padding: 20px;
	}
}

.leadsSwiper {
	padding-bottom: 70px;

	@media (min-width: 768px) {
		padding-top: 20px;
	}

	@media (min-width: 1200px) {
		padding-bottom: 120px;
	}


	.swiper-wrapper {
		margin-left: 0.75rem;

		@media (min-width: 768px) {
			//margin-left: 1.5rem;
			margin-left: 0;
		}
	}

	.swiper-slide {
		overflow: visible;
		border-radius: 10px;
		background-color: transparent;
		padding: 0;
		box-shadow: none;
		min-height: 300px;
		width: calc(100% - 60px) !important;
		display: flex;
		flex-direction: column;

		@media (min-width: 375px) {
			//height: 263px;
			padding: 20px 20px 24px 58px;
		}

		@media (min-width: 576px) {
			//height: 280px;
			width: calc(100% - 160px) !important;
		}

		@media (min-width: 768px) {
			//height: unset;
			width: calc(100% - 30px) !important;
			padding: 25px 25px 30px 75px;
			min-height: 355px;
		}

		@media (min-width: 992px) {
			width: calc(100% / 2 + 100px) !important;
			padding: 25px 25px 30px 62px;
		}

		@media (min-width: 1200px) {
			width: calc(100% / 2 + 100px) !important;
			padding: 64px 64px 64px 129px;
		}

		@media (min-width: 1400px) {
			width: calc(100% / 2 + 100px) !important;
		}

		@media (min-width: 1500px) {
			width: calc(100% / 2 + 50px) !important;
		}

		@media (min-width: 1600px) {
			width: calc(100% / 2 - 20px) !important;
		}

		@media (min-width: 1700px) {
			width: calc(100% / 2 - 75px) !important;
		}

		@media (min-width: 1900px) {
			width: calc(100% / 2 - 8vw) !important;
		}

		@media (min-width: 2000px) {
			width: calc(100% / 2 - 160px) !important;
		}

		@media (min-width: 2100px) {
			width: calc(100% / 2 - 220px) !important;
		}

		@media (min-width: 2400px) {
			width: calc(100% / 2 - 15vw) !important;
		}

		.our-offers__slider-card {
			border-radius: 10px;
			background-color: #fff;
			padding: 20px 20px 20px 20px;
			box-shadow: 0 20px 41px 0 rgba(79, 34, 107, 0.12);
			height: 160px;

			@media (min-width: 768px) {
				height: 190px;
			}

			@media (min-width: 1200px) {
				height: 238px;
				padding: 30px 25px;
			}

			@media (min-width: 1600px) {
				height: 245px;
			}

			@media (min-width: 1900px) {
				//height: 524px;
			}


			&:not(:first-child) {
				margin-top: 20px;
			}

			.offer-card__text {
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 6;
				-webkit-box-orient: vertical;
				overflow: hidden;

				@media (min-width: 1200px) {
					font-size: 15px;
				}
			}
		}
	}

	.swiper-scrollbar__wrap {
		position: relative;
		width: calc(100% - 130px);
		left: 50%;
		transform: translateX(-50%);

		@media (min-width: 768px) {
			width: 200px;
			transform: translateX(-45%);
		}

		@media (min-width: 1200px) {
			position: relative;
			bottom: -26px;
			width: 400px;
			transform: translateX(-30%);
		}

		@media (min-width: 1400px) {
			transform: translateX(-24%);
		}

		@media (min-width: 1600px) {
			width: 300px;
			transform: none;
			left: 336px;
		}
	}

	.swiper-scrollbar {
		bottom: -28px;

		@media (min-width: 1200px) {
			bottom: -40px;
		}

		&::before, &::after {
			width: 70px;
			display: none;

			@media (min-width: 768px) {
				width: 75px;
			}
		}
	}

	.swiper-button-next,
	.swiper-button-prev {
		top: auto;
		bottom: 24px
	}

	.swiper-button-next {
		right: 0.75rem;

		@media (min-width: 768px) {
			right: calc(50% - 160px);
		}

		@media (min-width: 1200px) {
			right: calc(50% - 350px);
		}

		@media (min-width: 1400px) {
			right: calc(50% - 375px);
		}

		@media (min-width: 1600px) {
			left: 665px;
		}
	}

	.swiper-button-prev {
		left: 0.75rem;

		@media (min-width: 768px) {
			left: calc(50% - 140px);
		}

		@media (min-width: 1200px) {
			left: calc(50% - 190px);
		}

		@media (min-width: 1400px) {
			left: calc(50% - 170px);
		}

		@media (min-width: 1600px) {
			left: 250px;
		}
	}
}