.main-hero2 {

	.main-hero__col-2::before {
		background-image: url('../img/decor/main-hero2-back.png');
	}

	.main-hero__col-2-wrap {
		@media (min-width: 1700px) {
			max-width: 532px;
		}
	}

	.main-hero__form {
		@media (min-width: 992px) {
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
		}
	}

	.main-hero__form-btn {
		@media (min-width: 1400px) {
			max-width: max-content;
		}

		span {
			@media (min-width: 992px) {
				font-size: 18px;
			}

			@media (min-width: 1400px) {
				font-size: 22px;
			}
		}
	}
}

.main-hero__form {
	textarea {
		height: 92px;
		resize: none;
		width: 100%;

		@media (min-width: 992px) {
			height: 140px;
		}

		@media (min-width: 1200px) {

		}
	}
}




