.text-block {
	overflow: clip;
	margin-top: -80px;
	padding: 30px 0 65px;

	@media (min-width: 768px) {
		padding: 40px 0 65px;
		margin: 0;
	}

	@media (min-width: 992px) {
		padding: 60px 0;
	}

	@media (min-width: 1200px) {
		padding: 80px 0;
	}
}

.text-block__img-col {
	margin-top: -20px;

	@media (min-width: 768px) {
		margin: 0;
	}
}

.text-block__image {
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	width: 100%;
	height: calc(200px + 40vw);
	background-image: url('../img/decor/rouble.png');
	position: relative;
	left: -15px;

	@media (min-width: 576px) {
		height: 430px;
	}

	@media (min-width: 768px) {
		height: 100%;
		width: 855px;
		left: -360px;
		top: 50%;
		transform: translateY(-44%);
		opacity: 0.5;
	}

	@media (min-width: 992px) {
		left: -230px;
		opacity: 0.75;
	}

	@media (min-width: 1200px) {
		left: -130px;
		opacity: 1;
	}

	@media (min-width: 1600px) {
		width: 760px;
		left: -70px;
		height: calc(100% + 180px);
	}

	@media (min-width: 1900px) {
		left: -60px;
	}
}

.text-block__title {
	color: #64458D;
}

.text-block__descr {
	@media (min-width: 768px) {
		position: relative;
		z-index: 2;
		font-size: 14px;
	}

	p {
		@media (min-width: 768px) {
			margin-bottom: 10px;
		}
	}
}

.shadow-btn-wrap {
	margin-top: 20px;
	border-radius: 100px;
	box-shadow: inset 0px -11px 11px 0px rgba(255, 255, 255, 0.25),
	0px 20px 41px 0px rgba(216, 202, 250, 0.85);
	width: fit-content;

	@media (min-width: 1400px) {
		margin-top: 30px;
	}
}

.text-block__btn {
	position: relative;
	z-index: 1;
}