.faq-block {
	overflow: hidden;
	padding: 30px 0 60px;

	@media (min-width: 768px) {
		padding: 40px 0;
	}

	@media (min-width: 992px) {
		padding: 60px 0;
	}

	@media (min-width: 1200px) {
		padding: 80px 0;
	}

	@media (min-width: 1600px) {
		min-height: 860px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		background-size: cover;
	}

	.accordion {
		padding: 10px 0;

		@media (min-width: 768px) {
			padding: 0;
		}

		@media (min-width: 1400px) {
			margin-top: 30px;
		}
	}

	.accordion-item {
		border: 1px solid rgb(230, 221, 238) !important;
		border-radius: 14px;
		overflow: clip;
		margin-bottom: 12px;

		@media (min-width: 992px) {
			&:last-child {
				margin-bottom: 0 !important;
			}
		}

		@media (min-width: 1400px) {
			margin-bottom: 26px;
		}
	}

	.accordion-header {
		display: flex;
		padding: 0;
	}

	.accordion-button {
		font-size: 14px;
		font-weight: 700;
		border: none;
		box-shadow: none;
		padding-bottom: 20px;
		padding-top: 20px;
		background-color: transparent;

		@media (min-width: 1400px) {
			font-size: 16px;
			padding-bottom: 20px;
			padding-top: 20px;
		}

		&:focus {
			outline: none;
			box-shadow: none;
		}

		&:not(.collapsed) {
			background-color: transparent;
		}

		&::after {
			content: none;
		}

		&[aria-expanded="true"] {
			& + .accordion__icon {
				&::before {
					transform: rotate(180deg);
				}

				&::after {
					transform: rotate(180deg);
				}
			}
		}
	}

	.accordion-body {
		padding-top: 0;
		color: rgb(104, 112, 134);
		font-size: 14px;
		//font-weight: 300;

		@media (min-width: 1400px) {
			font-size: 16px;
			font-weight: 300;
		}
	}

	& + .our-partners {
		padding: 0 0 60px;
	}
}

.faq-block__col-q {
	@media (min-width: 1600px) {
		width: 60%;
	}

	@media (min-width: 1700px) {
		//width: 55%;
	}
}

.faq-block__col-i {
	@media (min-width: 1600px) {
		width: 40%;
	}

	@media (min-width: 1700px) {
		//width: 45%;
	}
}

.faq-block__title {
	color: rgb(100, 69, 141);;
}

.accordion__icon {
	min-width: 36px;
	min-height: 36px;
	width: 36px;
	height: 36px;
	border-radius: 50%;
	border: 1px solid rgba(0,0,0,0.5);
	align-self: center;
	margin-right: 15px;
	position: relative;

	&::before, &::after {
	  position: absolute;
	  content: '';
	  top: 49.5%;
		left: 25%;
	  width: 50%;
	  height: 2px;
	  background-color: rgb(100, 69, 141);
		transition: all .2s ease;
	}

	&::after {
		transform: rotate(90deg);
	}
}

.faq-block__additional {
	position: relative;

	@media (min-width: 768px) {
		height: 100%;
		display: flex;
		flex-direction: column;
		position: relative;
	}
}

.faq-block__image {
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	background-image: url('../img/decor/faq.png');
	position: relative;
	width: 120%;
	height: calc(206px + 40vw);
	left: -16vw;
	pointer-events: none;

	@media (min-width: 400px) {
		left: -18vw;
	}

	@media (min-width: 445px) {
		left: -20vw;
	}

	@media (min-width: 576px) {
		left: calc(50% - 380px);
		width: 622px;
		height: 437px;
	}

	@media (min-width: 768px) {
		left: auto;
		width: 590px;
		height: 430px;
		right: calc(50% - 360px);
		position: absolute;
		top: 32px;
	}

	@media (min-width: 992px) {
		width: 605px;
		height: 410px;
		right: calc(50% - 322px);
		position: absolute;
		top: 10px;
	}

	@media (min-width: 1400px) {
		width: 615px;
		height: 485px;
		top: 40px;
		right: calc(50% - 368px);
	}

	@media (min-width: 1600px) {
		width: 595px;
		height: 468px;
		top: 28px;
		right: calc(50% - 368px);
	}

	@media (min-width: 1700px) {
		top: 35px;
	}

}

.faq-block__btn-wrap {
	position: relative;
	margin-top: -60px;

	@media (min-width: 768px) {
		margin-top: auto;
		//position: relative;
		//bottom: 12px;
		position: absolute;
		top: 357px;
	}

	@media (min-width: 992px) {
		top: 383px;
	}

	@media (min-width: 1200px) {
		width: 100%;
		display: flex;
		justify-content: center;
		top: 360px;
	}

	@media (min-width: 1400px) {
		top: 443px;
		width: calc(100% + 120px);
	}

	@media (min-width: 1600px) {
		top: 422px;
	}

	.shadow-btn-wrap {

		@media (min-width: 992px) {
			margin-top: 0;
		}

		a {
			@media (min-width: 992px) {
				font-size: 17px;
				padding: 14px 25px;
			}

			@media (min-width: 1400px) {
				font-size: 24px;
			}
		}
	}
}

