.text-block2 {
	background-repeat: no-repeat;
	background-position: center bottom;
	background-size: 2150px;
	background-image: url('../img/decor/plate-bg.png');
	color: #fff;
	overflow: hidden;
	position: relative;
	padding: 30px 0;

	@media (min-width: 768px) {
		padding: 40px 0;
	}

	@media (min-width: 992px) {
		padding: 60px 0;
	}

	@media (min-width: 1200px) {
		padding: 80px 0;
	}

	@media (min-width: 1600px) {
		min-height: 860px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		background-size: cover;
	}

	&::before {
		@media (min-width: 768px) {
			position: absolute;
			content: "";
			top: 56%;
			transform: translateY(-53%);
			left: calc(50% - 858px);
			width: calc(470px + 40vh);
			height: calc(100% + 40vh);
			background-repeat: no-repeat;
			background-position: top 40px left;
			background-size: cover;
			background-image: url('../img/decor/plates.png');
		}

		@media (min-width: 992px) {
			top: 59%;
			left: calc(50% - 935px);
		}

		@media (min-width: 1200px) {
			top: 61%;
			left: calc(50% - 1092px);
			width: calc(470px + 58vh);
			height: calc(100% + 58vh);
		}

		@media (min-width: 1600px) {
			top: 76%;
			left: calc(50% - 1629px);
			width: calc(470px + 101vh);
			height: calc(100% + 101vh);
		}

		@media (min-width: 1900px) {
			top: 77%;
			left: calc(50% - 90vw);
			width: calc(470px + 110vh);
			height: calc(100% + 110vh);
		}

		@media (min-width: 2500px) {
			top: 85%;
			left: calc(50% - 85vw);
		}
	}
}

.text-block2__btn-wrap {
	display: flex;
	gap: 20px;
	margin-top: 20px;

	@media (min-width: 768px) {
		margin-top: 30px;
	}

	@media (min-width: 1200px) {
		margin-top: 40px;
	}

	@media (min-width: 1400px) {
		gap: 40px;
	}
}

.text-block2__content-col {
	position: relative;
	z-index: 2;
}

.text-block2__descr {
	text-shadow: 1px 1px 5px #36066f;
}